<template>
  <PageContent>
    <div slot="form" class="page-form">
      <form-item label="">
        <el-input placeholder="" v-model="searchParams.Payload" clearable @keyup.enter.native="search"></el-input>
      </form-item>
      <div class="btn-box">
        <el-button size="small" type="primary" @click="search"></el-button>
        <el-button size="small" @click="isShowAddDialog=true"></el-button>
      </div>
    </div>
    <div class="page-main">
      <ListLayout>
        <el-table slot="list" :data="tableData" width="100%" :border="false">
          <el-table-column label="Host" prop="AgentHost"></el-table-column>
          <el-table-column label="Ip" prop="AgentIp"></el-table-column>
          <el-table-column label="" prop="ProxyPort"></el-table-column>
          <el-table-column label="" prop="ProtocolType"></el-table-column>
          <el-table-column label="" prop="DestPort"></el-table-column>
          <el-table-column label="" width="80px" show-overflow-tooltip>
            <template v-slot="{row}">
              <i class="el-icon-loading warning--text mr-1" v-if="row.Status === 1"></i>
              <span v-if="statusMap[row.Status]" :class="statusMap[row.Status].color">{{statusMap[row.Status].text}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="CreateTime">
              <template v-slot="{row}"> 
                <div>{{$moment(row.CreateTime * 1000).format('YYYY/MM/DD hh:mm:ss')}}</div>
              </template>
            </el-table-column>
          <el-table-column label="" width="140px">
            <template v-slot="{row}">
              <el-button type="danger" size="mini" icon="el-icon-delete" circle @click="deleteRow(row, row.TransparentProxyId)"></el-button>
              <el-tooltip class="item" effect="dark" content="" placement="bottom" v-if="row.Status===2">
                <el-button type="primary" size="mini" icon="el-icon-top" circle @click="onlineHandler(row)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="bottom" v-if="[1,3].includes(row.Status)">
                <el-button type="danger" size="mini" icon="el-icon-bottom" circle @click="offlineHandler(row)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="bottom">
                <el-button type="primary" size="mini" icon="esign-icon-relation" circle @click="probeHandler(row)"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
          <div class="table-empty" slot="empty" style="width: 100%;">
            <img src="@/assets/images/empty.png" alt="" width="305" height="159" />
          </div>
        </el-table>
        <Pagination slot="pagination" @size-change="sizeChange" @current-change="getTableData" :total="pagination.totalCount" :pageSize="pagination.size" :current-page.sync="pagination.page" />
      </ListLayout>
    </div>
    <!--  -->
    <AddDialog v-if="isShowAddDialog"  @close="isShowAddDialog=false" @save="search" />
  </PageContent>
</template>

<script>
import pageList from '@/mixins/pageList';
import AddDialog from './AddDialog.vue';

export default {
  components: {AddDialog},

  mixins: [pageList],

  data () {
    return {
      searchUrl: this.$apis.transparentProxySearch,
      apiUrl: this.$apis.transparentProxyCreate,
      statusMap: {
        1: { text: '', color: 'grey--text' },
        2: { text: '', color: 'red--text' },
        3: { text: '', color: 'green--text' }
      },
      isShowAddDialog: false
    }
  },

  created () {
    this.search();
    this.intervalRefresh();
  },

  methods: {
    /**
     * 
     */
    onlineHandler(row) {
      this.loading = true;
      this.$axios.post(`${this.$apis.transparentProxyOnline}/${row.TransparentProxyId}`).then(res => {
        this.loading = false;
        this.$message.success('');
        this.search();
      }).catch(err => {
        this.loading = false;
      });
    },

    /**
     * 
     */
    offlineHandler(row) {
      this.loading = true;
      this.$axios.post(`${this.$apis.transparentProxyOffline}/${row.TransparentProxyId}`).then(res => {
        this.loading = false;
        this.$message.success('');
        this.search();
      }).catch(err => {
        this.loading = false;
      });
    },

    /**
     * 
     */
    probeHandler(row) {
      this.loading = true;
      this.$axios.get(`${this.$apis.transparentProxyProbe}/${row.TransparentProxyId}`).then(res => {
        this.loading = false;
        this.$message.success('');
        this.search();
      }).catch(err => {
        this.loading = false;
        this.search();
      });
    },
  }
}
</script>