<template>
  <PageContent v-loading="loading">
    <div slot="form" class="page-form">
      <form-item label="">
        <el-input placeholder="" v-model="searchParams.Payload" clearable @keyup.enter.native="search"></el-input>
      </form-item>
      <form-item label="">
        <el-select placeholder="" v-model="searchParams.ServerType" clearable>
          <el-option v-for="item in ServiceTypeOptions" :key="item.value" :label="item.text" :value="item.value"></el-option>
        </el-select>
      </form-item>
      <form-item label="IP">
        <el-input placeholder="IP" v-model="searchParams.AttackIP" clearable @keyup.enter.native="search"></el-input>
      </form-item>
      <form-item label="">
        <el-date-picker v-model="searchParams.dateRange" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss" range-separator=""
          start-placeholder="" end-placeholder="" />
      </form-item>
      <div class="btn-box">
        <el-button size="small" type="primary" @click="search"></el-button>
        <el-button size="small" @click="resetParams"></el-button>
      </div>
    </div>
    <div class="page-main">
      <ListLayout>
        <el-table slot="list" :data="tableData" width="100%" height="100%" :border="false">
          <el-table-column label="" prop="BaitName"></el-table-column>
          <el-table-column label="" prop="TokenType"></el-table-column>
          <el-table-column label="" prop="FileName"></el-table-column>
          <el-table-column label="IP" prop="OpenIP"></el-table-column>
          <el-table-column label="" prop="OpenTime">
          <template v-slot="{row}">
              <div>{{$moment(row.OpenTime * 1000).format('YYYY/MM/DD hh:mm:ss')}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="Location"></el-table-column>
          <el-table-column label="UA" prop="UserAgent"></el-table-column>
          <div class="table-empty" slot="empty" style="width: 100%;">
            <img src="@/assets/images/empty.png" alt="" width="305" height="159" />
          </div>
        </el-table>
        <Pagination slot="pagination" @size-change="sizeChange" @current-change="getTableData" :total="pagination.totalCount" :pageSize="pagination.size" :current-page.sync="pagination.page" />
      </ListLayout>
    </div>
  </PageContent>
</template>


<script>
import pageList from '@/mixins/pageList';
import {ServiceTypeOptions} from '@/lib/config';

export default {
  mixins: [pageList],
  data () {
    return {
      searchUrl: this.$apis.tokenTracktSearch,
      // 
      searchParams: {
        Payload: '',
        ServerType: '',
        AttackIP: '',
        dateRange: []
      },
      // 
      ServiceTypeOptions
    }
  },

  created() {
    this.search();
  },

  methods: {
    /**
     * 
     */
    getSearchParams() {
      let StartTime, EndTime;
      if (this.searchParams.dateRange) {
        StartTime = this.searchParams.dateRange[0];
        EndTime = this.searchParams.dateRange[1];
      }
      return {
        Payload: this.searchParams.Payload,
        ServerType: this.searchParams.ServerType,
        AttackIP: this.searchParams.AttackIP,
        StartTime,
        EndTime
      }
    },
  }
}
</script>