<template>
  <PageContent v-loading="loading">
    <div slot="form" class="page-form">
      <form-item label="">
        <el-input placeholder="" v-model="searchParams.Payload" clearable @keyup.enter.native="search"></el-input>
      </form-item>
      <div class="btn-box">
        <el-button size="small" type="primary" @click="search"></el-button>
        <el-button size="small" @click="isShowAddDialog=true"></el-button>
      </div>
    </div>
    <div class="page-main">
      <ListLayout>
        <el-table slot="list" :data="tableData" width="100%" height="100%" :border="false">
          <el-table-column label="" prop="HoneypotName"></el-table-column>
          <el-table-column label="" prop="PodName"></el-table-column>

          <el-table-column label="" prop="ProtocolType"></el-table-column>
          <el-table-column label=" IP" prop="HoneypotIp" show-overflow-tooltip></el-table-column>
          <el-table-column label="" width="80px" show-overflow-tooltip>
            <template v-slot="{row}">
              <i class="el-icon-loading  mr-1" v-if="row.Status === 1"></i>
              <span v-if="statusMap[row.Status]" :class="statusMap[row.Status].color">{{statusMap[row.Status].text}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="CreateTime" width="220px" show-overflow-tooltip>
            <template v-slot="{row}">
              <div>{{$moment(row.CreateTime * 1000).format('YYYY/MM/DD hh:mm:ss')}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" width="170">
     
            <template v-slot="{row}">
           
                <el-button type="danger" size="mini" icon="el-icon-delete" circle @click="deleteRow(row, row.HoneypotId)"></el-button>
                <el-tooltip content="" placement="bottom">
                  <el-button type="primary" size="mini" icon="esign-icon-icon-test" circle @click.native="showBaitDialog(row)"></el-button>
                </el-tooltip>
       
            </template>
          </el-table-column>
          <div class="table-empty" slot="empty" style="width: 100%;">
            <img src="@/assets/images/empty.png" alt="" width="305" height="159" />
          </div>
        </el-table>
        <Pagination slot="pagination" @size-change="sizeChange" @current-change="getTableData" :total="pagination.totalCount" :pageSize="pagination.size" :current-page.sync="pagination.page" />
      </ListLayout>
    </div>
    <!--  -->
    <AddDialog v-if="isShowAddDialog" :imageAddressOptions="imageAddressOptions" @close="isShowAddDialog=false" @save="search" />
    <!--  -->
    <DetailDialog v-if="detailDialogCtrl.isShow" :detailData="detailDialogCtrl.data" @close="detailDialogCtrl.isShow=false" />
    <!--  -->
    <BaitListDialog v-if="baitListDialogCtrl.isShow" :ServerID="baitListDialogCtrl.id" @close="baitListDialogCtrl.isShow=false" />
  </PageContent>
</template>

<script>
import AddDialog from './AddDialog.vue';
import DetailDialog from './DetailDialog.vue';
import BaitListDialog from './BaitListDialog.vue';
import pageList from '@/mixins/pageList';

export default {
  components: {
    AddDialog,
    DetailDialog,
    BaitListDialog
  },

  mixins: [pageList],

  data () {
    return {
      searchUrl: this.$apis.honeypotSearch,
      apiUrl: this.$apis.honeypotCreate,
      isShowAddDialog: false,
      detailDialogCtrl: {
        isShow: false,
        data: {}
      },
      // 
      imageAddressOptions: [],
      // 
      tokenListDialogCtrl: {
        isShow: false,
        id: ''
      },
      // 
      baitListDialogCtrl: {
        isShow: false,
        id: ''
      }
    }
  },
  created () {
    this.search();
    this.getImageAddressOptions();
    this.intervalRefresh();
  },
  methods: {
    /**
     * 
     */
    getImageAddressOptions () {
      this.$axios.get(this.$apis.getImagesPod).then(res => {
        this.imageAddressOptions = res.data;
      });
    },
    async podStatusCheck (record) {
      try {
        const res = await this.$Server('/testpodstatus', 'POST', {
          honeypotid: record.honeypotid
        })
        const { code, message } = res
        if (code === 1001) {
          record.statusText = ''
          this.$message.error(message)
        } else {
          this.$message.success(message)
        }
      } catch (e) {
        record.statusText = ''
      }
    },

    /**
     * 
     */
    showDetail (row) {
      this.loading = true
      this.$axios.get(`${this.$apis.honeypotCreate}/${row.HoneypotId}`).then(res => {
        this.loading = false;
        this.detailDialogCtrl = {
          isShow: true,
          data: res.data
        }
      }).catch(() => {
        this.loading = false;
      });
    },
    /**
     * 
     */
    showBaitDialog ({ HoneypotId }) {
      this.baitListDialogCtrl = {
        isShow: true,
        id: HoneypotId
      };
    }
  }
}
</script>

<style scoped>
</style>
