import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/views/Base'
import SubLayout from '@/components/layout/SubLayout';

import Login from '@/views/login/login';
import trafficAttack from '../views/threatenPerception/trafficAttack'; // 
import falcoEvent from '../views/threatenPerception/falcoEvent'; // FALCO
import tokenTrack from '../views/threatenPerception/tokenTrack'; // 
import traceSource from '../views/threatenPerception/traceSource'; // 
import HoneypotList from '@/views/honeypot-manage/HoneypotList'; // 
import HoneypotTopo from '@/views/honeypot-manage/HoneypotTopo'; // 
import signList from '@/views/trapManage/signList'; // 
import trapList from '@/views/trapManage/trapList'; // 
import imageList from '@/views/imageList'; // 
import probesList from '@/views/probesList'; // 
import protocolProxy from '@/views/shadowProxy/protocolProxy'; // 
import protocolType from '@/views/shadowProxy/protocolType'; // 
import protocolTransparent from '@/views/shadowProxy/protocolTransparent'; // 
import systemConfig from '@/views/systemConfig'; // 
import DownLoad from '@/views/download' // 
import DataV from '@/views/datav/DataV' // 


const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

const router = new VueRouter({
  base: 'decept-defense',
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      name: 'Base',
      component: Layout,
      redirect: '/threaten-perception',
      children: [
        {
          path: '/threaten-perception',
          name: 'threatenPerception',
          component: SubLayout,
          redirect: '/threaten-perception/traffic-attack',
          meta: { title: '' },
          children: [
            {
              path: 'traffic-attack',
              name: 'trafficAttack',
              meta: { title: '' },
              component: trafficAttack
            },
            {
              path: 'falco-event',
              name: 'falcoEvent',
              meta: { title: 'Falco' },
              component: falcoEvent
            },
            {
              path: 'token-track',
              name: 'tokenTrack',
              meta: { title: '' },
              component: tokenTrack
            },
            {
              path: 'trace-source',
              name: 'traceSource',
              meta: { title: '' },
              component: traceSource
            },
            {
              path: 'honeypot-topo',
              name: 'HoneypotTopo',
              meta: { title: '' },
              component: HoneypotTopo,
            },
          ]
        },
        {
          path: 'honeypots',
          name: 'honeypotList',
          meta: { title: '' },
          component: HoneypotList
        },
        {
          path: '/probes-list',
          name: 'probesList',
          meta: { title: '' },
          component: probesList
        },
        {
          path: 'proxy-manage',
          name: 'proxyManage',
          meta: { title: '' },
          component: SubLayout,
          redirect: '/proxy-manage/protocol-forwarding',
          children: [
            {
              path: 'protocol-forwarding',
              name: 'protocol-forwarding',
              meta: { title: '' },
              component: protocolProxy,
            },
            {
              path: 'protocol-transparent',
              name: 'protocolTransparent',
              meta: { title: '' },
              component: protocolTransparent,
            },
          ]
        },
        {
          path: '/trap-manage',
          name: 'TrapManage',
          component: SubLayout,
          redirect: '/trap-manage/sign',
          meta: { title: '' },
          children: [
            {
              path: 'token',
              name: 'sign',
              meta: { title: '' },
              component: signList
            },
            {
              path: 'trap',
              name: 'trap',
              meta: { title: '' },
              component: trapList
            },
            {
              path: 'protocol-type',
              name: 'protocolType',
              meta: { title: '' },
              component: protocolType,
            },
            {
              path: 'image-list',
              name: 'imageList',
              meta: { title: '' },
              component: imageList
            },
          ]
        },
        {
          path: '/system-config',
          name: 'systemConfig',
          meta: { title: '' },
          component: systemConfig
        },
        {
          path: '/download',
          name: 'Download',
          component: DownLoad,
          meta: { title: '' },
        },
      ]
    },

    {
      path: '/datav',
      name: 'DataV',
      component: DataV
    },
    
  ],
})

export default router
