<template>
  <PageContent v-loading="loading">
    <div slot="form" class="page-form">
      <form-item label="">
        <el-input placeholder="" v-model="searchParams.Payload" clearable @keyup.enter.native="search"></el-input>
      </form-item>
      <div class="btn-box">
        <el-button size="small" type="primary" @click="search"></el-button>
        <el-button size="small" @click="isShowAddDialog=true"></el-button>
      </div>
    </div>
    <div class="page-main">
      <ListLayout>
        <el-table slot="list" :data="tableData" width="100%" height="100%" :border="false">
          <el-table-column label="" prop="BaitName"></el-table-column>
          <el-table-column label="" prop="FileName"></el-table-column>
          <el-table-column label="" prop="BaitType"></el-table-column>
          <el-table-column label="" width="80px" show-overflow-tooltip>
            <template v-slot="{row}">
              <span v-if="tokenAbleMap[row.TokenAble]" :class="tokenAbleMap[row.TokenAble].color">{{tokenAbleMap[row.TokenAble].text}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="CreateTime">
            <template v-slot="{row}">
              <div>{{$moment(row.CreateTime * 1000).format('YYYY/MM/DD hh:mm:ss')}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" width="130px">
            <template v-slot="{row}">
              <el-button type="danger" size="mini" icon="el-icon-delete" circle @click="deleteRow(row, row.BaitId)"></el-button>
              <el-tooltip class="item" effect="dark" content="" placement="bottom">
                <el-button v-if="row.TokenTraceUrl ===''" type="primary" size="mini" icon="el-icon-chicken" circle @click="signToken(row, row.BaitId)"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="bottom">
              <el-button v-if="row.BaitType==='FILE'" type="primary" size="mini" icon="el-icon-download" circle @click="downloadFile(row)"></el-button>
            </el-tooltip>
            </template>
          </el-table-column>
          <div class="table-empty" slot="empty" style="width: 100%;">
            <img src="@/assets/images/empty.png" alt="" width="305" height="159" />
          </div>
        </el-table>
        <Pagination slot="pagination" @size-change="sizeChange" @current-change="getTableData" :total="pagination.totalCount" :pageSize="pagination.size" :current-page.sync="pagination.page" />
      </ListLayout>
    </div>
    <!--  -->
    <AddBaitDialog v-if="isShowAddDialog" @close="isShowAddDialog=false" @save="search" />
  </PageContent>
</template>

<script>
import AddBaitDialog from './AddBaitDialog.vue';
import pageList from '@/mixins/pageList';

export default {
  components: { AddBaitDialog },

  mixins: [pageList],

  data () {
    return {
      searchUrl: this.$apis.trapSearch,
      apiUrl: this.$apis.trapCreate,
      signTokenUri: this.$apis.trapSignToken,
      isShowAddDialog: false
    }
  },

  created() {
    this.search();
  },

  methods: {
    /**
     *    
     */
    signToken (row, baitId, confirmName) {
      console.log("--------------------")
      console.log(baitId)
      this.$confirm(` ${row[confirmName] || ''}?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.loading = true;
        if (baitId) {
          return this.$axios.put(`${this.signTokenUri}/${baitId}`);
        } 
        return this.$axios.put(`${this.signTokenUri}/${row.BaitId}`);
      }).then(res => {
        this.search();
        this.$message.success('');
      }).catch(err => {
        this.loading = false;
        console.log(err)
      });
    },
  }
}
</script>