<template>
  <PageContent v-loading="loading">
    <div slot="form" class="page-form">
      <form-item label="">
        <el-input placeholder="" v-model="searchParams.Payload" clearable @keyup.enter.native="search"></el-input>
      </form-item>
      <form-item label="">
        <el-select placeholder="" v-model="searchParams.Type">
          <el-option v-for="item in TypeOptions" :key="item" :label="item" :value="item" />
        </el-select>
      </form-item>
      <form-item label="">
        <el-input placeholder="IP" v-model="searchParams.HoneypotName" clearable @keyup.enter.native="search"></el-input>
      </form-item>
      <form-item label="IP">
        <el-input placeholder="IP" v-model="searchParams.AttackIp" clearable @keyup.enter.native="search"></el-input>
      </form-item>

      <!-- <form-item label="">
        <el-input placeholder="" v-model="searchParams.ProtocolType" clearable @keyup.enter.native="search"></el-input>
      </form-item> -->

      <form-item label="">
        <el-select v-model="searchParams.ProtocolType">
          <el-option v-for="item in imageTypeOptions" :key="item" :label="item" :value="item" />
        </el-select>
      </form-item>

      <form-item label="">
        <el-date-picker v-model="searchParams.dateRange" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss" range-separator=""
          start-placeholder="" end-placeholder="" />
      </form-item>
      <div class="btn-box">
        <el-button size="small" type="primary" @click="search"></el-button>
        <el-button size="small" @click="resetParams"></el-button>
      </div>
    </div>
    <div class="page-main" style="display:block;overflow:auto">
        <el-card shadow="never" class="my-3 mr-2" v-for="(item,index) in tableData" :key="index">
          <el-row>
            <el-col :span="4">
              <span class="desc-field">: </span>{{ item.Type}}
            </el-col>
            <el-col :span="5">
              <span class="desc-field">: </span><span style="font-weight: bold">{{ item.HoneypotName}}</span>
            </el-col>
            <el-col :span="5">
              <span class="desc-field">IP: </span><span style="font-weight: bold">{{item.AttackIp}}</span>
            </el-col>
            <el-col :span="4">
              <span class="desc-field">: </span>{{ item.ProtocolType}}
            </el-col>
            <el-col :span="6">
              <span class="desc-field">: {{$moment(item.EventTime * 1000).format('YYYY/MM/DD hh:mm:ss')}}</span>
            </el-col>

            <el-col :span="24" class="mt-1">
              <span class="desc-field">: </span>{{ item.Log}}<a class="primary--text ml-2" style="cursor: pointer;" @click="showDetail(item)"></a>
            </el-col>
          </el-row>
        </el-card>
    </div>
    <!--  -->
    <DetailDialog v-if="detailDialogCtrl.isShow" :detailData="detailDialogCtrl.data" @close="detailDialogCtrl.isShow=false" />
  </PageContent>
</template>

<script>
import DetailDialog from './DetailDialog.vue';
import pageList from '@/mixins/pageList';

export default {
  components: { DetailDialog },

  mixins: [pageList],

  data () {
    return {
      searchUrl: this.$apis.trafficAttackSearch,
      searchParams: {
        Payload: '',
        Type: '',
        HoneypotName: '',
        AttackIp: '',
        ProtocolType: '',
        dateRange: []
      },
      imageTypeOptions: [],

      TypeOptions: ['Falco', 'Attack'],
      // 
      prevScrollTop: 0,
      // 
      isEnd: false,
      detailDialogCtrl: {
        isShow: false,
        data: {}
      },
    }
  },

  created () {
    this.getImageTypeOptions();
    this.search();
  },

  mounted () {
    // let container = this.$el.querySelector('.el-table__body-wrapper');
    let container = this.$el.querySelector('.page-main');
    container.addEventListener('scroll', this.onScroll);
  },

  methods: {

    getImageTypeOptions () {
      this.$axios.get(this.$apis.getProtocolType).then(res => {
        this.imageTypeOptions = res.data;
      });
    },
    /**
     * 
     */
    onScroll ({ target }) {
      // 
      if (this.isEnd) return false;
      let scrollTop = target.scrollTop;
      // 
      if (scrollTop < this.prevScrollTop) {
        this.prevScrollTop = scrollTop;
      } else {
        this.prevScrollTop = scrollTop;
        if (this.loading) return false;
        if ((target.scrollTop + target.clientHeight) > (target.scrollHeight - 10)) {
          this.loading = true;
          setTimeout(() => {
            this.getTableData();
          }, 100)
        }
      }
    },

    search () {
      this.isEnd = false;
      this.pagination.page = 1;
      this.tableData = [];
      this.getTableData();
    },

    /**
     * 
     */
    getSearchParams() {
      let StartTime, EndTime;
      if (this.searchParams.dateRange) {
        StartTime = this.searchParams.dateRange[0];
        EndTime = this.searchParams.dateRange[1];
      }
      return {
        Payload: this.searchParams.Payload,
        Type: this.searchParams.Type,
        HoneypotName: this.searchParams.HoneypotName,
        AttackIp: this.searchParams.AttackIp,
        ProtocolType: this.searchParams.ProtocolType,
        StartTime,
        EndTime
      }
    },

    getTableData () {
      this.loading = true;
      this.$axios.post(this.$apis.traceSourceSearch, {
        ...this.getSearchParams(),
        PageNumber: this.pagination.page,
        PageSize: 50
      }).then(({ data }) => {
        this.loading = false;
        let copyData = data.List || [];
        if (copyData.length > 0) {
          ++this.pagination.page;
          this.tableData.push(...copyData);
        } else {
          this.isEnd = true;
        }
      }).catch(err => {
        this.loading = false;
      });
    },

    /**
     * 
     */
    showDetail ({ Detail }) {
      let data = {};
      if (Detail) {
        data = JSON.parse(Detail);
      }
      this.detailDialogCtrl = {
        isShow: true,
        data
      }
    },

  }
}
</script>

<style lang="less" scoped>
/deep/ .el-card__body{
  padding: 10px;
  font-size: 13px;
  line-height: 1.6em;
  .desc-field {
    font-weight: 500;
    color: #666;
  }
}
</style>