<template>
  <div class="map-box" id="echartsMap"></div>
</template>

<script>
import echarts from 'echarts'
import 'echarts/map/js/world'
import debounce from 'lodash/debounce'

const geoCoordMap = {
  '': [120.13066322374, 30.240018034923],
  '': [-4.388361, 11.186148],
  '': [-118.24311, 34.052713],
  '': [114.195466, 22.282751],
  '': [-87.801833, 41.870975],
  '': [-4.62829, 7.72415],
  '': [-1.657222, 51.886863],
  '': [10.01959, 54.38474],
  '': [45.326912, 41.101891],
  '': [89.116876, 67.757906],
  '': [-48.678945, -10.493623],
  '': [31.815593, 31.418032],
  '': [2.175129, 41.385064],
  '': [104.88659, 11.545469],
  '': [9.189948, 45.46623],
  '': [-56.162231, -34.901113],
  '': [32.608571, -25.893473],
  '': [3.054275, 36.753027],
  '': [55.269441, 25.204514],
  '': [17.108519, 48.179162],
  '': [150.993137, -33.675509],
  '': [-121.910642, 41.38028],
  '': [144.999416, -37.781726],
  '': [-99.094092, 19.365711],
  '': [-123.023921, 49.311753]
}
export default {
  name: 'Map',
  props: {
    el: {
      type: String,
      default: '',
    },
    chartData: {
      type: Array,
      default: () => [],
    }
  },
  data() {
    return {
      init: false
    }
  },
  mounted() {
    this.initChart()
    window.addEventListener('resize', this.resize)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resize)
  },
  methods: {
    resize() {
      debounce(() => {
        console.log('resize')
        this.myChart.resize()
      }, 500)()
    },
    initChart() {
      if (this.init) {
        setTimeout(() => {
          this.myChart.resize()
        }, 0)

        return
      }

      this.init = true

      const $el = document.getElementById('echartsMap')

      this.myChart = echarts.init($el)

      this.setOption()
    },
    setOption() {
      const config = this.getConfig()

      if (!this.chartData.length && !this.myChart) return

      //  [,]
      const attacked = []

      // 
      const attackLine = []

      // 
      const beAttacked = [{
        name: '',
        value: [120.13066322374, 30.240018034923]
      }]

      this.chartData.forEach(item => {
        item.value = 3000

        //  [,]
        const startPos = [item.longitude, item.latitude]

        //  [,]
        const endPos = [120.13066322374, 30.240018034923]

        attackLine.push([
          //  [,]
          { coord: startPos, value: item.value },
          // 
          { coord: endPos },
        ])

        attacked.push({
          name: `${ item.country }-${ item.province }`,
          // [value]
          value: startPos.concat([item.value])
        })

      })

      // 
      config.series[0].data = attacked

      // 
      config.series[1].data = attackLine

      // 
      config.series[2].data = beAttacked

      this.myChart.setOption(config)
    },
    getConfig() {

      const series = [
        // 
        {
          type: 'effectScatter',
          coordinateSystem: 'geo',
          zlevel: 5,
          rippleEffect: {
            //
            period: 4, //
            brushType: 'stroke', // stroke, fill
            scale: 4 //
          },
          label: {
            normal: {
              show: false,
              position: 'right', //
              offset: [5, 0], //
              formatter: '{b}' //
            },
            emphasis: {
              show: true
            }
          },
          symbol: 'circle',
          symbolSize: function (val) {
            console.log('val', val)
            return 4 + val[2] / 1000 // 
          },
          itemStyle: {
            color: 'rgba(255, 100, 59, 1)'
          },
          data: []
        },
        // 
        {
          type: 'lines',
          zlevel: 2,
          effect: {
            show: true,
            period: 4, //
            trailLength: 0.02, //[0,1]
            symbol: 'arrow', //
            symbolSize: 5 //
          },
          lineStyle: {
            color: 'rgba(255, 100, 59, 1)',
            width: 1, //
            opacity: 0, //
            curveness: 0.3 //
          },
          data: []
        },
        // 
        {
          type: 'scatter',
          coordinateSystem: 'geo',
          zlevel: 2,
          label: {
            normal: {
              show: true,
              position: 'right',
              color: '#00ffff',
              formatter: '{b}',
              textStyle: {
                color: '#00ffff'
              }
            },
            emphasis: {
              show: true
            }
          },
          symbol:
            'path://M17.673 0l17.455 7.493v11.35c0 2.6-.44 5.13-1.325 7.583a24.148 24.148 0 0 1-9.175 11.888 21.011 21.011 0 0 1-6.952 3.051 21.006 21.006 0 0 1-6.955-3.051 24.13 24.13 0 0 1-9.175-11.89A22.228 22.228 0 0 1 .22 18.84V7.496L17.676 0h-.003zm0 20.639V4.082L4.083 9.96v10.679h13.595v16.78a15.892 15.892 0 0 0 5.024-2.4 19.45 19.45 0 0 0 4.038-3.883 21.12 21.12 0 0 0 4.529-10.497H17.673z',
          symbolSize: [16, 20],
          itemStyle: {
            color: '#00ffff'
          },
          data: []
        }
      ]

      return {
        tooltip: {
          show: false,
          trigger: 'item',
          backgroundColor: '#1540a1',
          borderColor: '#FFFFCC',
          showDelay: 0,
          hideDelay: 0,
          enterable: true,
          transitionDuration: 0,
          extraCssText: 'z-index:100',
          formatter: function (params) {
            console.log('params', params)
            //
            var res = ''
            var name = params.name
            var value = params.value[params.seriesIndex + 1]
            res =
              '<span style=\'color:#fff;\'>' +
              name +
              '</span><br/>' +
              value
            return res
          }
        },
        geo: {
          map: 'world',
          label: {
            emphasis: {
              show: false
            }
          },
          roam: true, //
          layoutCenter: ['50%', '50%'], //
          layoutSize: '180%',
          itemStyle: {
            normal: {
              color: '#04284e', //
              borderColor: '#5bc1c9' //
            },
            emphasis: {
              color: 'rgba(37, 43, 61, .5)' //
            }
          }
        },
        series: series
      }
    }
  }
}
</script>

<style scoped>
.map-box {
  width: 100%;
  height: 100%;
}
</style>
