import {statusMap, tokenAbleMap, agentStatusMap, lostStatusMap} from '@/lib/config.js';

export default {
  data () {
    return {
      // 
      statusMap,
      // 1
      tokenAbleMap,
      // 2
      agentStatusMap,

      lostStatusMap,
      // 
      loading: false,
      // url
      searchUrl: '',
      // url
      apiUrl: '',
      // 
      searchParams: {
        Payload: ''
      },
      cloneParams: {},
      // 
      pagination: {
        page: 1,
        size: 10,
        totalCount: 0
      },
      // 
      tableData: [],
      // 
      intervalTimer: 0
    }
  },

  created() {
    this.cloneParams = {...this.searchParams};
  },

  destroyed() {
    clearInterval(this.intervalTimer);
  },

  methods: {
    /**
     * 
     */
    intervalRefresh() {
      this.intervalTimer = setInterval(() => {
        this.getTableData(false);
      }, 2000);
    },

    /**
     * 
     */
    resetParams() {
      this.searchParams = {...this.cloneParams};
    },

    /**
     * 
     * @param {Number} size 
     */
    sizeChange (size) {
      this.pagination.size = size;
      this.search();
    },

    /**
     * 
     */
    search () {
      this.pagination.page = 1;
      this.getTableData();
    },

    /**
     * 
     */
    getSearchParams() {
      return this.searchParams;
    },

    /**
     * 
     */
    getTableData (loading=true) {
      this.loading = loading;
      this.$axios.post(this.searchUrl, {
        ...this.getSearchParams(),
        PageNumber: this.pagination.page,
        PageSize: this.pagination.size
      }).then(({ data }) => {
        this.loading = false;
        this.tableData = data.List || [];
        this.pagination.totalCount = data.Count;
      }).catch(err => {
        this.loading = false;
      });
    },

    /**
     * 
     * @param {Object} row 
     * @param {String} specificId Id
     * @param {String} confirmName 
     * 
     */
    deleteRow (row, specificId, confirmName) {
      console.log("--------------------")
      console.log(specificId)
      this.$confirm(` ${row[confirmName] || ''}?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.loading = true;
        if (specificId) {
          return this.$axios.delete(`${this.apiUrl}/${specificId}`);
        } 
        return this.$axios.delete(`${this.apiUrl}/${row.ID}`);
      }).then(res => {
        this.search();
        this.$message.success('');
      }).catch(err => {
        this.loading = false;
        console.log(err)
      });
    },

    /**
     * 
     */
     downloadFile({BaitId}) {
      this.$axios.get(`${this.apiUrl}/${BaitId}`).then(res => {
        window.open(res.data);
      })
    },

    /**
     * 
     */
     close () {
      this.$emit('close');
    }
  }
}